
#app {
  margin: auto;
  height: 100vh;
}
body {
  overflow: hidden;
  height: 100vh;
}
.head > label {
  height: 60px;
  display: inline-block;
  width: calc(100% / 8 - 4px);
}
.head > label > input {
  width: 98%;
  height: 26px;
  border-width: 2px;
  padding: 1px 0;
  cursor: pointer;
}
canvas {
  border: 1px solid #BBB;
}



